.Control {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 20px 0px;
    margin-bottom: 15px;
    background: #9D65C9;
}

.Control:hover {
    cursor: pointer;
    opacity: .75;
}

.Control p {
    color: white;
    font-size: 36px;
}

@media only screen and (min-width: 900px)
{
    .Control {
        
        width: 45%;
    }
}