.Score {
    display: flex;
    justify-content: space-evenly;
    width: 75%;
    height: 50px;
    margin: 20px auto;
}

.ScoreBar {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 180px;
    height: 100%;
    margin: 0px 10px;
}

.WrongBar {
    background: #DF7B75;
}

.CorrectBar {
    background: #68C371;
}

.ScoreBar p {
    color: white;
    font-size: 24px;
}

@media only screen and (min-width: 900px)
{
    .Score {
        margin: 0px auto 50px;
    }

    .ScoreBar {
        width: 100%;
        margin: 0px;
    }
}