.KanjiViewer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 250px;
    background: #5D54A4;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, .25);
    margin: 0px auto;
}

.KanjiViewer p {
    font-size: 144px;
    text-shadow: 0px 4px 10px rgba(0, 0, 0, .25);
    color: #CE95FB;
}