.AnswerStatus{
    display: flex;
    justify-content: center;
    align-items: center;

    color: white;
    font-size: 1.25rem;
    font-weight: bold;
    height: 60px;
    width: 80%;
    margin: 0px auto;
}

.CorrectStatus{
    background: #68C371;
    animation: fadeIn .5s forwards ease-in-out;
}

.WrongStatus{
    background: #DF7B75;
    animation: fadeIn .5s forwards ease-in-out;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@media only screen and (min-width: 750px)
{
    .AnswerStatus {
        font-size: 2rem;
    }
}
